export function isParenthesisBalanced(
  str: string,
): boolean {
  const stack1: Array<string> = []
  const stack2: Array<string> = []
  const arr = str.split('')

  for (const char of arr) {
    if (char === '(') {
      stack1.push(char)
    }
    if (char === ')') {
      stack2.push(char)
    }
  }

  return stack1.length === stack2.length
}
